import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { OrderStateCategoryEnum } from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { OrderNotificationSound } from '../../../molecules/OrderNotificationSound'
import { KitchenInfoMessage } from '../atoms/KitchenInfoMessage'
import { KitchenCompletionOrderBox } from '../molecules/KitchenCompletionOrderBox'
import { useKitchenCompletionOrderList } from './KitchenCompletionOrderListLoader'

interface IProps {
  category: OrderStateCategoryEnum
}

export const KitchenCompletionOrderList = observer<IProps>(({ category }) => {
  const { order: orderModel } = useRoot()
  const { loading, noFilter, orders } = useKitchenCompletionOrderList()

  if (noFilter) {
    return (
      <KitchenInfoMessage>
        <Trans>Select a branch</Trans>
      </KitchenInfoMessage>
    )
  }

  if (loading) {
    return (
      <KitchenInfoMessage>
        <Trans>Loading</Trans>
      </KitchenInfoMessage>
    )
  }

  const categoryOrders = orders.filter(
    RM.propEq('orderStateCategory', category),
  )

  if (categoryOrders.length === 0) {
    switch (category) {
      case OrderStateCategoryEnum.NewOrders:
        return (
          <KitchenInfoMessage>
            <Trans>No new orders</Trans>
          </KitchenInfoMessage>
        )
      case OrderStateCategoryEnum.Preparing:
        return (
          <KitchenInfoMessage>
            <Trans>No orders to complete</Trans>
          </KitchenInfoMessage>
        )
      case OrderStateCategoryEnum.ReadyToDeliver:
        return (
          <KitchenInfoMessage>
            <Trans>No orders to hand over</Trans>
          </KitchenInfoMessage>
        )
      default:
        return (
          <KitchenInfoMessage>
            <Trans>No orders</Trans>
          </KitchenInfoMessage>
        )
    }
  }

  const shouldPlayNotification =
    category === OrderStateCategoryEnum.NewOrders && categoryOrders.length > 0

  return (
    <div data-testid={`order-list-${category}`}>
      {categoryOrders.map(order => (
        <KitchenCompletionOrderBox
          key={order.id}
          order={order}
          showBranch={orderModel.list.branchesFilter.length > 1}
        />
      ))}
      <OrderNotificationSound shouldPlay={shouldPlayNotification} />
    </div>
  )
})
