import { Trans } from '@lingui/macro'
import React from 'react'
import styled from 'styled-components/macro'

import { LinkStyled } from '../../../atoms/LinkStyled'
import { DeliveryTypeEnum } from '../../../graph/generated'
import { FKitchenOrder } from '../../../graph/types'
import { formatAddress } from '../../../helpers/formats'
import { Colors, FontSizes, Radiuses, Spacing } from '../../../styling'
import { CustomerIdentity } from '../../order/atoms/CustomerIdentity'
import { DeliveryTimeBadge } from '../../order/atoms/DeliveryTimeBadge'
import { ToBranchContainerStyled } from '../../order/molecules/OrderTicker'
import { KitchenOrderDetail } from './KitchenOrderDetail'

interface IProps {
  order: FKitchenOrder.Fragment
}

interface IProps {
  order: FKitchenOrder.Fragment
  showBranch: boolean
}
export const StyledKitchenOrderBox = styled.div`
  font-size: ${FontSizes.CompactCard};
  background-color: ${Colors.white};
  color: ${Colors.greyDarkest};
  border-radius: ${Radiuses.Card};
  margin-bottom: ${Spacing.Middle};
  margin-right: ${Spacing.Middle};
  position: relative;

  .order-ticker {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding: 0.2rem;
    color: ${Colors.greyMiddle};
  }

  .delivery-time-badge-container {
    flex: 0 0 1.8rem;
  }

  .address-container {
    flex: 1 0;
    padding: 0 0 0 0.5rem;
  }

  .customer-identity-container {
    flex: 1 0;
    padding: 0 0 0 0.5rem;
  }

  .pickup-label {
    font-style: italic;
  }
`

export const KitchenOrderBox: React.FC<IProps> = ({ order, showBranch }) => {
  const getOrderDestination = () => {
    if (
      order.deliveryType &&
      order.deliveryType.enum === DeliveryTypeEnum.Pickup
    ) {
      return (
        <div className="pickup-label">
          <Trans>To pickup</Trans>
        </div>
      )
    }
    return formatAddress(order.address)
  }

  return (
    <StyledKitchenOrderBox>
      {showBranch && (
        <ToBranchContainerStyled>
          {order.companyBranch.name}
        </ToBranchContainerStyled>
      )}
      <div className="order-ticker">
        <div className="delivery-time-badge-container">
          <DeliveryTimeBadge
            deliverAt={order.deliverAt ? new Date(order.deliverAt) : null}
            deliveredAt={order.deliveredAt ? new Date(order.deliveredAt) : null}
            isCancelled={false}
            isDelayed={order.delayedDeliveryTime !== null}
          />
        </div>

        <div className="address-container">{getOrderDestination()}</div>

        <div className="customer-identity-container">
          <CustomerIdentity customer={order.customer} />
          <br />
          {order.customer && order.customer.defaultPhone ? (
            <LinkStyled
              href={`tel:${order.customer.defaultPhone.phone
                .toString()
                .replace('/ /g', '')}`}
            >
              {order.customer.defaultPhone.phone}
            </LinkStyled>
          ) : null}
        </div>
      </div>

      <KitchenOrderDetail order={order} />
    </StyledKitchenOrderBox>
  )
}
