import React from 'react'
import styled from 'styled-components/macro'

import { CardDock } from '../../../atoms/CardDock'
import {
  OrderStateCategoryEnum,
  OrderStateEnum,
} from '../../../graph/generated'
import { FKitchenCompletionOrder } from '../../../graph/types'
import { Colors, FontSizes, Radiuses, Spacing } from '../../../styling'
import { ToBranchContainerStyled } from '../../order/molecules/OrderTicker'
import { KitchenOrderWareList } from '../organisms/KitchenOrderWareList'
import { KitchenOrderStarIndicator } from './KitchenOrderStarIndicator'
import { KitchenSetStateButton } from './KitchenSetStateButton'

interface IProps {
  order: FKitchenCompletionOrder.Fragment
}

interface IProps {
  order: FKitchenCompletionOrder.Fragment
  showBranch: boolean
}
export const StyledKitchenCompletionOrderBox = styled.div`
  font-size: ${FontSizes.CompactCard};
  background-color: ${Colors.white};
  color: ${Colors.greyDarkest};
  border-radius: ${Radiuses.Card};
  margin-bottom: ${Spacing.Middle};
  margin-right: ${Spacing.Middle};
  padding-bottom: ${Spacing.Middle};
  position: relative;

  & .header {
    display: flex;
    flex-wrap: nowrap;
  }

  & .order__number {
    width: 4.5rem;
    background: ${Colors.orange};
    color: ${Colors.white};
    font-size: 2rem;
    border-top-left-radius: ${Radiuses.Card};
    text-align: center;
  }

  & .order__source {
    display: inline-block;
    background: ${Colors.greyMiddle};
    color: ${Colors.white};
    font-size: 0.9rem;
    padding: 0.1rem 0.8rem;
    border-radius: 0.9rem;
    margin: 0.5rem;
  }

  & .order__content {
    margin: ${Spacing.Middle};
  }
`

export const KitchenCompletionOrderBox: React.FC<IProps> = ({
  order,
  showBranch,
}) => {
  return (
    <StyledKitchenCompletionOrderBox data-testid="order-box">
      {showBranch && (
        <ToBranchContainerStyled>
          {order.companyBranch.name}
        </ToBranchContainerStyled>
      )}
      <div className="header">
        <div className="order__number">{order.orderNumber}</div>
        <div className="order__source">{`${order.orderOrigin.code} ${
          order.cashBox !== null ? order.cashBox.id : ''
        }`}</div>
        {order.isPriority && <KitchenOrderStarIndicator />}
      </div>

      <div className="order__content">
        <KitchenOrderWareList
          orderWares={order.items}
          isCheckable={
            order.orderStateCategory === OrderStateCategoryEnum.Preparing ||
            order.orderStateCategory === OrderStateCategoryEnum.ReadyToDeliver
          }
        />
      </div>

      {order.orderStateCategory === OrderStateCategoryEnum.NewOrders && (
        <CardDock position="BottomRight">
          <KitchenSetStateButton
            orderId={order.id}
            color={Colors.green}
            setState={OrderStateEnum.Accepted}
          />
        </CardDock>
      )}

      {order.orderStateCategory === OrderStateCategoryEnum.ReadyToDeliver && (
        <CardDock position="BottomRight">
          <KitchenSetStateButton
            orderId={order.id}
            color={Colors.red}
            setState={OrderStateEnum.Finished}
          />
        </CardDock>
      )}
    </StyledKitchenCompletionOrderBox>
  )
}
