import React from 'react'
import styled from 'styled-components/macro'

import { CardDock } from '../../../atoms/CardDock'
import { HorizontalSeparator } from '../../../atoms/HorizontalSeparator'
import {
  DeliveryTypeEnum,
  OrderStateCategoryEnum,
  OrderStateEnum,
} from '../../../graph/generated'
import { FKitchenOrder } from '../../../graph/types'
import { Colors } from '../../../styling'
import { NoteLine } from '../../order/molecules/NoteLine'
import { KitchenOrderAtSummary } from '../atoms/KitchenOrderAtSummary'
import { KitchenOrderWareList } from '../organisms/KitchenOrderWareList'
import { KitchenOrderStarIndicator } from './KitchenOrderStarIndicator'
import { KitchenSetStateButton } from './KitchenSetStateButton'

interface IProps {
  order: FKitchenOrder.Fragment
}

const StyledOrderDetailContainer = styled.div`
  padding: 0 0.2rem 0.1rem 0.2rem;
  position: relative;
`

export const KitchenOrderDetail: React.FC<IProps> = ({ order }) => (
  <StyledOrderDetailContainer>
    <HorizontalSeparator />
    {order.isPriority && <KitchenOrderStarIndicator />}
    <KitchenOrderWareList
      orderWares={order.items}
      isCheckable={
        order.orderStateCategory === OrderStateCategoryEnum.Preparing ||
        order.orderStateCategory === OrderStateCategoryEnum.ReadyToDeliver
      }
    />
    <HorizontalSeparator />
    <KitchenOrderAtSummary
      deliverTime={order.deliverAt}
      deliverType={
        order.deliveryType &&
        order.deliveryType.enum === DeliveryTypeEnum.Pickup
          ? 'PERSONAL'
          : 'DELIVER_UNTIL'
      }
    />
    <NoteLine note={order.note} previousNote={order.previousNote} />
    {order.orderStateCategory === OrderStateCategoryEnum.NewOrders && (
      <CardDock position="BottomRight">
        <KitchenSetStateButton
          orderId={order.id}
          color={Colors.green}
          setState={OrderStateEnum.InProduce}
        />
      </CardDock>
    )}
  </StyledOrderDetailContainer>
)
