import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import { OrderStateEnum, useMOrderState } from '../../../graph/generated'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'

interface IProps {
  orderId: ID
  color: Colors
  setState: OrderStateEnum
}

export const KitchenSetStateButton: React.FC<IProps> = ({
  orderId,
  color,
  setState,
}) => {
  const [acceptOrder] = useMOrderState()

  return (
    <CanMutate mutationName="addOrderState">
      <TouchButton
        iconName="keyboard_arrow_right"
        width={Spacing.ButtonHeight + Spacing.DefaultSizeUnit}
        color={color}
        onClick={() => {
          acceptOrder({ orderId, state: setState })
        }}
      />
    </CanMutate>
  )
}
