import React from 'react'
import { Route, RouteComponentProps, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { KitchenCompletionOverviewPage } from '../pages/KitchenCompletionOverviewPage'
import { KitchenOverviewPage } from '../pages/KitchenOverviewPage'

interface IProps extends RouteComponentProps<{}> {}

export const KitchenRoutes: React.FC<IProps> = ({ match }) => {
  return (
    <>
      <Switch>
        <SecureRoute
          exact={true}
          path={match.path}
          component={KitchenOverviewPage}
        />
        <SecureRoute
          exact={true}
          path={`${match.path}/completion`}
          component={KitchenCompletionOverviewPage}
        />
        <Route component={NotFoundPage} />
      </Switch>
    </>
  )
}

export default KitchenRoutes
